<template>
  <div class="row"> 
    <div class="col-sm-12 col-md-6 col-lg-3 col-xl-3 mt-5">
      <EditCliente v-if="edit" />
      <PCliente v-else />
    </div>
    <div class="col-sm-12 col-md-6 col-lg-9 col-xl-9 mt-5">
      <div v-if="id !='0'">
        <BuscarNumeroSeleccion/>
        <tableNumeroCliente/>
      </div>
      <div v-else>
        <h1>No ha sido escogido el numero</h1>
      </div>
    </div>
</div>
 
  

  
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters, mapMutations, mapState } from 'vuex'
export default {
  props:{
    id:{
      type: String,
      required: true
    }
  },
  components:{
    PCliente : defineAsyncComponent(()=> import('@/modules/rifa/components/cliente/PCliente.vue')),
    BuscarNumeroSeleccion: defineAsyncComponent(()=> import('@/modules/rifa/components/numeros/buscarNumeroSeleccion.vue')),
    tableNumeroCliente : defineAsyncComponent(()=> import('@/modules/rifa/components/numeros/tablaNumeroCliente.vue')),
    EditCliente: defineAsyncComponent(()=> import('@/modules/rifa/components/cliente/EditCliente.vue'))
  },
  computed:{
    ...mapGetters('cliente',['getClient']),
    ...mapState('cliente', ['isLoading', 'edit']),
  },
  methods:{
    ...mapActions('cliente',['getCliente']),
    ...mapMutations('cliente',['restaurarCliente','setPanel']),
    ...mapActions('numeros',['getNumerosCliente']),  
  },
  
  created(){
    if(this.id !='0'){
      this.getCliente(this.id)
      this.getNumerosCliente(this.id)
    }
    this.setPanel(true)

  },
  beforeUnmount(){
    this.restaurarCliente()
    this.setPanel(false)
  }
  
}
</script>

